<template>
  <div
    class="banner"
    @mousemove="move"
    @mouseenter="enter"
    @mouseleave="leave"
    ref="domBanner"
  >
    <div
      class="layer"
      :class="item.name"
      v-for="item in pictures"
      :key="item.name"
    >
      <img
        v-if="item.link"
        :src="item.link"
        :style="{ width: `${itemWidth}px` }"
        alt=""
      />
      <video
        autoplay
        loop
        muted
        v-if="item.video"
        :style="{ width: `${itemWidth}px` }"
        :src="item.video"
      ></video>
    </div>

    <div class="layer canvas">
      <CanvasSnow :style="{ width: `${itemWidth}px` }" :percent="percent" />
    </div>
  </div>
</template>

<script>
import CanvasSnow from "../components/Canvas";
import { ref } from "vue";
export default {
  name: "banner",
  components: { CanvasSnow },
  props: {
    pictures: Object,
  },

  setup() {
    let domBanner = ref("");
    let enterX = 0;
    let percent = ref(0);

    const enter = (e) => {
      enterX = e.clientX;
      domBanner.value.classList.add("moving");
    };

    const leave = () => {
      percent.value = 0;
      domBanner.value.style.setProperty("--per", 0);
      domBanner.value.classList.remove("moving");
    };

    const move = (e) => {
      Move(e, enterX, domBanner.value);
    };

    var locked = false;
    const Move = (e, enterX, dom) => {
      if (!locked) {
        locked = true;
        requestAnimationFrame(() => {
          percent.value = (e.clientX - enterX) / dom.clientWidth;
          dom.style.setProperty("--per", percent.value);
          locked = false;
        });
      }
    };

    let itemWidth = ref(window.innerWidth * 1.2);
    const onResize = () => {
      itemWidth.value = window.innerWidth * 1.2;
    };
    window.addEventListener("resize", onResize);

    return { domBanner, move, enter, leave, itemWidth, percent };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.banner {
  width: 100%;
  min-width: 999px;
  min-height: 155px;
  height: 9.375vh;
  position: absolute;
  overflow: hidden;

  --per: 0;

  .layer {
    width: 100%;
    height: 100%;
    position: absolute;

    display: flex;
    align-items: center;
    justify-content: center;
    will-change: transform, opacity;
    img {
      height: 100%;
      display: block;
      object-fit: cover;
    }
    video {
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
}
</style>
<style lang="css">
.moving .layer {
  transition: none !important;
}

.layer {
  transform: translateX(calc((var(--per)) * -100px));
  transition: 0.5s ease-in all;
}
.snowBall {
  transform: none;
  opacity: calc(1 + var(--per) * 1.5);
}
.window {
  opacity: calc(0 + var(--per) * 1.5);
}
.winter_afternoon,
.tree_sunshine {
  opacity: calc(1 + var(--per) * 1.5);
}
.winter_evening,
.tree {
  opacity: calc(0 + var(--per) * 1.5);
}
</style>
