<template>
  <canvas class="snow_canvas"> </canvas>
</template>

<script>
import { onMounted } from "vue";
export default {
  props: {
    percent: {
      type: Number,
    },
  },

  setup(props) {
    class Snowflake {
      constructor() {
        this.x = 0;
        this.y = 0;
        this.vx = 0;
        this.vy = 0;
        this.radius = 0;
        this.alpha = 0;
        this.reset();
      }
      reset() {
        this.x = this.randBetween(0, window.innerWidth);
        this.y = this.randBetween(0, -window.innerHeight);

        this.vx = 0;
        // this.vy = this.randBetween(1, 3);
        this.radius = this.randBetween(1, 4);
        if (this.radius >= 2) {
          this.vy = this.randBetween(1, 1.5);
          this.alpha = this.randBetween(0.6, 1);
        } else {
          this.vy = this.randBetween(0.1, 1.2);
          this.alpha = this.randBetween(0.1, 0.9);
        }
        // this.alpha = this.randBetween(0.1, 0.9);
      }
      randBetween(min, max) {
        return min + Math.random() * (max - min);
      }
      update() {
        this.x += this.vx;
        this.y += this.vy;
        if (this.y + this.radius > window.innerHeight) {
          this.reset();
        }
      }
    }

    class Snow {
      constructor() {
        this.canvas = document.querySelector(".snow_canvas");
        this.ctx = this.canvas.getContext("2d");
        this.updateBound = this.update.bind(this);
        window.addEventListener("resize", () => {
          this.onResize();
        });
        this.onResize();
        this.createSnowflakes();
      }
      onResize() {
        this.canvas.width = window.innerWidth * 1.2;
        this.canvas.height = document.querySelector(
          ".snow_canvas"
        ).clientHeight;
      }
      createSnowflakes() {
        const flakes = window.innerWidth / 3;
        this.snowflakes = [];
        for (let s = 0; s < flakes; s++) {
          this.snowflakes.push(new Snowflake());
        }
      }

      update() {
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

        this.snowflakes.forEach((flake) => {
          flake.update();
          this.ctx.save();
          this.ctx.fillStyle = "#FFF";
          this.ctx.beginPath();

          if (flake.radius >= 3) {
            this.ctx.arc(
              flake.x + props.percent * 150,
              flake.y,
              flake.radius,
              0,
              Math.PI * 2
            );
          } else {
            this.ctx.arc(
              flake.x + props.percent * 70,
              flake.y,
              flake.radius,
              0,
              Math.PI * 2
            );
          }

          this.ctx.closePath();
          this.ctx.globalAlpha = flake.alpha;
          this.ctx.shadowColor = "#FFF";
          this.ctx.shadowBlur = 20;
          this.ctx.fill();
          this.ctx.restore();
        });
        // for (let flake of this.snowflakes) {

        // }

        this.reqFlag = requestAnimationFrame(this.updateBound);
      }
      start() {
        this.reqFlag = requestAnimationFrame(this.updateBound);
      }
      stop() {
        cancelAnimationFrame(this.reqFlag);
      }
    }

    onMounted(() => {
      let snow = new Snow();
      snow.start();
    });
  },
};
</script>

<style lang="scss" scoped>
.snow_canvas {
  width: 100%;
  height: 100%;
}
</style>
